import React from 'react'
import PropTypes from 'prop-types'
import { useArgs } from '@storybook/api';
import '../css/Checkbox.css'

export const Checkbox = ({ label, isDisabled, isIndeterminate, onChange, ...props}) => {
    const [{ checked }, updateArgs] = useArgs();
    const toggleChecked = () => updateArgs({ checked: !checked });

    const classs = "checkbox" + (isDisabled? "-disabled": "") + (isIndeterminate? "-indeterminate":"")
    return (
        <label>
          <input id="checkbox" className={classs} type="checkbox" checked={checked} disabled={isDisabled} indeterminate={isIndeterminate} onChange={toggleChecked} {...props}/>
          {label}
        </label>
      );
};

Checkbox.propTypes = {
    label: PropTypes.string,
    isDisabled: PropTypes.bool,
    isIndeterminate: PropTypes.bool,
    onChange: PropTypes.func,
    
}

Checkbox.defaultProps = {
    label: "this is a checkbox",
    isDisabled: false,
    isIndeterminate: false,
    onChange: undefined,
}